body {
  color: #333;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  min-width: 320px;
  min-height: 100vh;
  padding: 10px;
  background-color: #f1f1f1;
  background-image: radial-gradient(#000 1px, transparent 1px), radial-gradient(#000 1px, transparent 1px);
  background-size: 20px 20px;
  background-position: 0 0, 10px 10px;
  box-sizing: border-box;
}
@media (min-width: 660px) {
  body {
    padding: 20px;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

p {
  margin-top: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

svg {
  fill: currentColor;
}

[stroke-width] {
  fill: none;
  stroke: currentColor;
}

.overflow-hidden {
  overflow: hidden;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0 0 0.67em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.game {
  display: grid;
  border-radius: 10px;
}
@media (min-width: 660px) {
  .game {
    grid-template-columns: 200px 1fr;
  }
}
@media (min-width: 992px) {
  .game {
    grid-template-columns: 250px 1fr;
  }
}

.game__board {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
  align-items: center;
  gap: 10px;
  margin-top: 135px;
  padding: 10px;
  background-color: #fff;
  border: 2px solid #b3b3b3;
  border-radius: 10px;
  box-sizing: border-box;
  position: relative;
}
@media (min-width: 500px) {
  .game__board {
    margin-top: 100px;
  }
}
@media (min-width: 660px) {
  .game__board {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    margin-top: 0;
    padding: 20px;
    border-radius: 0 10px 10px 0;
  }
}

.game__card {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  padding: 9px;
  background-color: #009efa;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.5s;
  user-select: none;
}
.game__card--active {
  transform: rotateY(180deg);
}
.game__card--active .game__card-icon {
  opacity: 1;
}
@media (min-width: 660px) {
  .game__card {
    height: 125px;
  }
}

.game__card-icon {
  width: 65px;
  height: 65px;
  opacity: 0;
  transition: 0.5s;
  pointer-events: none;
}

.game__setting {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  width: 100%;
  padding: 10px 15px;
  background-color: #fff;
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
}
@media (min-width: 660px) {
  .game__setting {
    flex-direction: column;
    gap: 10px;
    max-height: calc(100vh - 40px);
    padding: 20px;
    border: 2px solid #b3b3b3;
    border-right: none;
    border-radius: 10px 0 0 10px;
    box-shadow: none;
    position: sticky;
    top: 20px;
  }
  .game__setting .game__setting-text--hide {
    display: inline-block;
  }
}

.game__setting-text {
  margin-bottom: 0;
}
.game__setting-text--hide {
  display: none;
}

.game__difficult-range {
  flex-grow: 1;
  width: 100%;
}
@media (min-width: 500px) {
  .game__difficult-range {
    width: auto;
  }
}
@media (min-width: 660px) {
  .game__difficult-range {
    flex-grow: 0;
    width: 100%;
  }
}

.game__start-button {
  margin-top: auto;
}

.game__resize-range {
  display: none;
  width: 100%;
}
@media (min-width: 660px) {
  .game__resize-range {
    display: block;
  }
}

.button {
  color: #fff;
  font-weight: 500;
  letter-spacing: 0.2px;
  width: 100%;
  padding: 10px 20px;
  background-color: transparent;
  background-image: url("../img/nature-sprite.png");
  background-size: 2300% 100%;
  border: 1px solid #000;
  border-radius: 10px;
  cursor: pointer;
  animation: ani2 0.5s steps(22) forwards;
}
.button:hover {
  animation: ani 0.6s steps(22) forwards;
}

@keyframes ani {
  from {
    color: rgba(0, 0, 0, 0);
    background-position: 0 0;
  }
  to {
    color: rgb(0, 0, 0);
    background-position: 100% 0;
  }
}
@keyframes ani2 {
  from {
    color: rgb(0, 0, 0);
    background-position: 100% 0;
  }
  to {
    background-position: 0 0;
  }
}